<template>
    <div class="main-content">
        <div class="header">
            <div class="menu-togger-wrapper" @click="menuToggle">
                <img src="../../assets/image/menuToggler.png" class="menu-toggler"/>
            </div>
            <div class="title-wrapper">
                <img src="../../assets/image/logo.png" class="logo"/>
                <img src="../../assets/image/title.png" class="title"/>
            </div>
        </div>
        <div class="body">
            <div class="slider-wrapper" :class="!isCollapse ? 'unfold' : '' ">
                <el-menu :default-active="activeRouter"   :collapse="isCollapse" :router="true">
                    <template v-for="menu in menus">
                        <template v-if="menu.children">
                            <el-submenu >
                                <template slot="title">
                                    <i class="menu.metaData.icon"></i>
                                    <span slot="title">{{menu.metaData.title}}</span>
                                </template>
                                <el-menu-item  v-for="childMenu in menu.children" :index="menu.path">
                                    <i class="childMenu.metaData.icon"></i>
                                    <span slot="title">{{childMenu.metaData.title}}</span>
                                </el-menu-item>
                            </el-submenu>
                        </template>
                        <template v-else>
                            <el-menu-item :index="menu.path">
                                <i :class="menu.metaData.icon"></i>
                                <span slot="title">{{menu.metaData.title}}</span>
                            </el-menu-item>
                        </template>
                    </template>
                </el-menu>
            </div>
            <div class="content-wrapper" :style="contentStyle">
                <router-view/>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    export default {
        name: "main",
        data() {
            return {
                isCollapse: false
            }
        },
        methods: {
            menuToggle() {
                this.isCollapse = !this.isCollapse
            }
        },
        computed: {
            ...mapState(['menus']),
            activeRouter() {
                if(this.$route.path.indexOf('videoPlaying') !== -1) {
                    return '/main/videoList'
                }else {
                    return this.$route.path
                }
            },
            contentStyle() {
                if(this.isCollapse) {
                    return {
                        'width': 'calc(100% - 62px)'
                    }
                }else {
                    return {
                        'width':'calc(100% - 269px)'
                }
                }
            }

        }
    }
</script>

<style scoped lang="scss" >
    .main-content {
        height: 100%;
    }
    .header {
        width: 100%;
        height: 64px;
        .menu-togger-wrapper {
            display: inline-block;
            height: 100%;
            width: 62px;
            /*#FA6868*/
            background-color:  #AB201F;
            color: white;
            text-align: center;
            font-size: 40px;
            vertical-align: top;
            .menu-toggler {
                width: 24px;
                height: 24px;
            }
        }
        .title-wrapper {
            display: inline-block;
            height: 100%;
            width: calc( 100% - 62px );
            background: linear-gradient(left, #F44747, #F55B32);
            vertical-align: top;
        }
        .logo{
            width: 40px;
            height: 40px;
            margin: 12px 25px;
        }
        .title {
            margin: 22px 0px;
        }
    }
    .body {
        display: flex;
        min-height: calc(100% - 64px);
        .slider-wrapper {
            padding-top: 30px;
            border-right: solid 1px #e6e6e6;
            &.unfold {
                width: 268px;
            }
        }
        .content-wrapper {
            background-color: #F7F7F7;
        }
    }
</style>